/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
h1,
h2,
p,
a,
img,
ul,
li,
form,
footer,
header,
nav,
section {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

footer,
header,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ul {
  list-style: none;
}

.section--info {
  text-align: center;
  width: 90%;
  max-width: 550px;
  position: relative;
  height: 100vh;
  margin: 0 auto;
  padding-top: 5em;
}

.section--logotype {
  padding: 7em 0 4em 0;
  background-image: url(../images/logo.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.section--info p {
  opacity: 0.7;
  font-size: 1.2em;
}

.section--info-content {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav--container {
  margin: 1em 0em 0 0;
  position: fixed;
  width: 2em;
  height: 28px;
  z-index: 999;
  right: 0.8em;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  background: transparent;
  z-index: 3;
}

#nav-icon3 {
  height: 30px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
}

#nav-icon3 span {
  display: block;
  position: absolute;
  height: 3px;
  width: 2em;
  background: #E94B87;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

#nav-icon3 span:nth-child(1) {
  top: 0px;
}

#nav-icon3 span:nth-child(2),
#nav-icon3 span:nth-child(3) {
  top: 12px;
}

#nav-icon3 span:nth-child(4) {
  top: 24px;
}

#nav-icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
  background: #E94B87;
}

#nav-icon3.open span:nth-child(2) {
  transform: rotate(45deg);
  background: #E94B87;
}

#nav-icon3.open span:nth-child(3) {
  transform: rotate(-45deg);
  background: #E94B87;
}

#nav-icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
  background: #E94B87;
}

.nav--overlay {
  position: fixed;
  display: none;
  height: 100%;
  width: 100%;
  background: #303168;
  opacity: 0.9;
  z-index: 1;
}

.nav--responsive-menu {
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

.nav--responsive-menu li {
  font-size: 3em;
  margin-bottom: 0.555em;
  text-align: center;
}

.nav--responsive-menu a {
  text-decoration: none;
  border-bottom: none;
  color: white;
}

.nav--responsive-menu a:hover {
  text-decoration: none;
  border-bottom: none;
  color: white;
}

footer {
  padding: 1.6em 0;
  text-align: center;
}

footer h2 {
  font-size: 1.6em;
  padding: 1.6em 0;
}

form {
  display: flex;
  flex-direction: row;
  padding: 0.8em 0;
  margin: 0 auto;
  max-width: 600px;
}

@media screen and (max-width: 500px) {
  form {
    display: block;
  }
}

#twitter,
#facebook {
  width: 38px;
  height: auto;
}

.section--about,
.section--location {
  background-color: white;
  color: #424242;
  z-index: 10;
  font-size: 1.4em;
  font-weight: 500;
  width: 90%;
  height: 90%;
  margin: 3em auto;
  max-width: 960px;
  box-shadow: 0 9px 38px rgba(0, 0, 0, 0.3), 0 6px 12px rgba(0, 0, 0, 0.22);
}

@media screen and (max-width: 600px) {
  .section--about,
  .section--location {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    box-shadow: none;
  }
}

.section--location a {
  color: #303168;
  text-decoration: none;
}

@media screen and (max-width: 600px) {
  .section--location a {
    font-size: 0.7em;
  }
}

.section--location {
  padding-top: 4em;
  text-align: center;
}

.section--about p {
  font-weight: 400;
  line-height: 1.6em;
  margin-bottom: 1em;
}

.section--about h1,
.section--students h1 {
  letter-spacing: 0.3em;
  color: #303168;
  font-size: 1.67em;
  font-weight: 500;
  padding: 0.75em 0em 1.5em 0em;
}

.container--blurb,
.container--logos,
.divider {
  padding: 2.5em 2em;
  padding-bottom: 1.5em;
  max-width: 800px;
  margin: 0 auto;
}

@media screen and (max-width: 600px) {
  .container--blurb,
  .container--logos,
  .divider {
    padding: 1em 1em;
    font-size: 0.7em;
  }
}

.divider {
  padding-top: 4em;
}

.container--location {
  padding-top: 2em;
}

.container--location p,
.section--location p {
  font-weight: 400;
  font-size: 0.9em;
  padding-top: 0.4em;
}

@media screen and (max-width: 600px) {
  .container--location p,
  .section--location p {
    font-size: 0.7em;
  }
}

.section--location h2 {
  color: #303168;
  font-size: 1.5em;
  padding: 0.75em 0em 1.3em 0em;
}

.container--logos p {
  margin: 0 auto;
  text-align: center;
  width: 70%;
}

#sheridanlogo {
  width: 39%;
  display: inline-block;
  float: left;
}

#dclogo {
  width: 47%;
  display: inline-block;
}

.detail-svg {
  width: 2em;
}

#map {
  height: 50vh;
}

.modal--outer {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  background: white;
  width: 90%;
  max-width: 500px;
  transform: translate(-50%, -50%);
}

.modal h2,
.modal p {
  color: black;
}

/*-------------------------------------*/

.modal {
  text-align: center;
  padding: 20px;
  box-shadow: 0 0px 38px rgba(0, 0, 0, 0.3), 0 0px 12px rgba(0, 0, 0, 0.22);
}

.modal img.portrait {
  max-width: 70%;
  height: auto;
  margin-top: 20px;
}

.modal .closeBtn img {
  width: 17px;
  height: 17px;
  transition: all 0.16s ease-out;
}

.modal .closeBtn img:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.modal .closeBtn {
  position: absolute;
  right: 0;
  top: 0;
  margin: 10px;
  padding: 5px;
}

.modal h1 {
  padding: 0;
  letter-spacing: normal;
  text-align: center;
  margin-top: 10px;
  font-size: 1.3em;
}

.modal h2 {
  margin-bottom: 1em;
  font-weight: 400;
  font-size: 0.9em;
}

.modal p {
  text-align: left;
  padding-top: 1em;
  padding-bottom: 1em;
  font-size: 0.9em;
  font-weight: 400;
  border-top: 1px solid #D1D3D4;
}

.modal ul {
  height: 80%;
  width: 100%;
}

.modal li {
  display: inline;
}

.modal li img {
  width: 45px;
  height: auto;
  padding: 5px;
  transition: all 0.16s ease-out;
}

.modal li img:hover {
  opacity: 0.6;
}

.modal .portfolio {
  display: inline-block;
  background: #E94B87;
  border-radius: 5px;
  color: white;
  text-decoration: none;
  font-size: 0.9em;
  padding: 8px 45px 8px 45px;
  transition: all 0.16s ease-out;
}

.modal .portfolio:hover {
  opacity: 0.6;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .modal {
    padding: 1em;
  }

  .modal-portrait img.portrait {
    margin: 0;
    max-height: 30%;
  }

  .modal h1 {
    margin-top: 0;
  }

  .modal h2 {
    margin-bottom: 0.5em;
  }

  .modal p {
    padding-top: 0.5em;
    padding-bottom: 0.5em;
  }

  .modal a.portfolio {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }
}

@media (min-width: 550px) {
  .modal p {
    margin-left: 2em;
    margin-right: 2em;
  }
}

@media (min-width: 960px) {
  .modal {
    text-align: left;
    max-width: 850px;
  }

  .modal img.portrait {
    max-width: 100%;
    margin-top: 0px;
  }

  .modal-portrait {
    width: 400px;
    float: left;
  }

  .modal-content {
    margin: 20px 55px 0px 420px;
  }

  .modal h1 {
    padding-top: 30px;
    text-align: left;
  }

  .modal p {
    margin: 0px;
  }

  .modal li:first-child {
    padding-left: 0;
  }

  .modal .portfolio {
    margin: 25px 0px 20px 0px;
    padding: 8px 50px 8px 50px;
  }
}

* {
  box-sizing: border-box;
}

body {
  position: relative;
  background: #303168;
  font-family: "Roboto", sans-serif;
  color: white;
  line-height: 1.25;
  font-weight: 300;
  font-size: 1em;
  font-size: 1em;
}

.container {
  width: 90%;
  max-width: 960px;
  margin: 0 auto;
}

form {
  display: flex;
  flex-direction: row;
  padding: 0.8em 0;
}

@media screen and (max-width: 500px) {
  form {
    display: block;
  }
}

input {
  -webkit-appearance: none;
}

input[type=email] {
  background: #3E3F76;
  border: 1px solid white;
  border-radius: 5px 0 0 5px;
  color: white;
  padding: 0.667em;
  width: 100%;
  font-size: 1em;
}

@media screen and (max-width: 500px) {
  input[type=email] {
    border-radius: 5px 5px 0 0;
  }
}

:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: white;
  opacity: 1;
  font-style: italic;
  font-weight: 300;
}

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: white;
  opacity: 1;
  font-style: italic;
  font-weight: 300;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
  font-style: italic;
  font-weight: 300;
}

input[type=submit] {
  padding: 0.4em 1.667em;
  border-radius: 0 5px 5px 0;
  background: white;
  color: #3E3F76;
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  border: 2px solid white;
  cursor: pointer;
  text-align: center;
}

input[type=submit]:hover {
  color: #E94B87;
}

@media screen and (max-width: 500px) {
  input[type=submit] {
    width: 100%;
    border-radius: 0 0 5px 5px;
  }
}

.social--icon {
  display: inline-block;
  margin: 1.6em 0.4em;
}

#bg--threejs {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

h1 {
  padding-top: 40px;
  text-align: left;
  color: #303168;
}

.section--students {
  background: white;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0 9px 38px rgba(0, 0, 0, 0.3), 0 6px 12px rgba(0, 0, 0, 0.22);
}

@media screen and (max-width: 600px) {
  .section--students {
    width: 100%;
    height: 100%;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.list .student {
  margin: 20px 1%;
  width: 23%;
}

@media (max-width: 600px) {
  .list .student {
    width: 48%;
  }
}

.list .student .photo {
  margin-bottom: 10px;
  position: relative;
  transition: opacity 0.16s ease-out;
}

.list .student .photo img {
  width: 100%;
  height: auto;
}

.list .student .photo img:nth-child(2) {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.list .student .name,
.list .student .title {
  text-align: left;
  font-size: 16px;
  font-weight: 500;
  color: #303168;
}

.list .student .first,
.list .student .last {
  display: inline;
}

@media (max-width: 600px) {
  .list .student .first,
  .list .student .last {
    display: block;
  }
}

.list .student .title {
  margin-top: 5px;
  font-size: 12px;
  font-weight: 400;
  color: #424242;
}

.list .student:hover img:nth-child(2) {
  cursor: pointer;
  opacity: 0.6;
}
